import React, { useState, useEffect } from "react";
import "../css/Review.css";
import ArticleForPage from "../components/ArticleForPage";
import ReviewCard from "../components/ReviewCard";
import ArticleMedium from "../components/ArticleMedium";
import axios from "../axios";
import requests from "../requests";
import Pagination from "./Pagination";
function Review({ fetchUrl }) {
  const [grid, setGrid] = useState([]);
  useEffect(() => {
    async function fetchData() {
      const request = await axios.get(
        "http://127.0.0.1:8000/api/trending/trending-list"
      );
      console.log(request.data);
      setGrid(request.data);

      return request;
    }

    fetchData();
  }, [fetchUrl]);
  return (
    <div>
      <div className="review_main">
        <div className="review">
          {grid.slice(7, 8).map((grid) => {
            return (
              <div className="grid">
                <div className="grid1">
                  <div className="grid_image1">
                    <img src="https://cdn.vox-cdn.com/thumbor/rjFfLVO0zMExq5x5LCfcAbW6Q10=/0x0:2040x1360/912x513/filters:focal(857x517:1183x843):format(webp)/cdn.vox-cdn.com/uploads/chorus_image/image/61483719/acastro_190913_3667_0001.8.jpg"></img>
                  </div>
                  <div className="grid_text1">
                    <h1>{grid.title}</h1>
                  </div>
                </div>
                <div className="grid2">
                  <div className="grid_image2">
                    <img src="https://cdn.vox-cdn.com/thumbor/1mOabYwedi-ovJeCtNjDzu00Js0=/0x0:2040x1360/912x513/filters:focal(857x517:1183x843):format(webp)/cdn.vox-cdn.com/uploads/chorus_image/image/68877154/vpavic_4291_20201113_0366.0.0.jpg"></img>
                  </div>
                  <div className="grid_text2">
                    <h1>
                      <h1>{grid.title}</h1>
                    </h1>
                  </div>
                </div>
                <br></br>
              </div>
            );
          })}

          <div className="article_section">
            <div>
              <ArticleForPage
                title="Microsoft"
                fetchUrl={requests.fetchAmazonList}
              ></ArticleForPage>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Review;
